.imgWrap {
  width: 100%;
  position: relative;
  padding: 75px 80px;
  margin-bottom: 90px;
  background: linear-gradient(180deg, #373737 0%, #131212 100%);

  @media (max-width: 768px) {
    height: 380px;
    padding: 0;
    margin-bottom: 68px;
  }

  img {
    position: absolute;
    right: 0;
    bottom: 0;

    @media (max-width: 768px) {
      width: 100%;
      max-height: 100%;
      right: -60px;
    }
  }

  > div {
    width: 535px;
    color: #fff;
    font-size: 20px;
    word-break: break-word;

    .title {
      font-size: 50px;
      font-weight: bold;
    }

    .subTitle {
      font-size: 24px;
      margin-bottom: 70px;
    }
  }
}

.content {
  font-size: 20px;
  color: #1e1e1e;
  padding: 0 50px;
  word-break: break-word;

  @media (max-width: 768px) {
    font-size: 18px;
    padding: 0 15px;

    .title,
    .subTitle {
      text-align: center;
    }

    .title {
      font-size: 32px;
      font-weight: bold;
    }

    .subTitle {
      margin: 5px 0 30px;
    }
  }

  > p,
  > div {
    margin-bottom: 25px;
  }
}
